#register {
  padding-top: 6rem;
}

.register__container {
  margin-top: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.register__container form {
  width: 600px;
  height: 500px;
  background: var(--color-blue);
  padding: 4rem;
  border-radius: 2rem;
}

.register__container form div {
  position: relative;
  margin-top: 4rem;
  margin-bottom: 2rem;
}

.register__container input {
  font-size: 1.5rem;
  width: 100%;
  padding: 1rem;
  border-radius: 0.4rem;
}

.register__container label {
  font-size: 1.5rem;
  position: absolute;
  bottom: .5rem;
  left: 1rem;
  color: #333;
  transition: var(--transition);
}

.register__container input:focus ~ label {
  bottom: 4rem;
  left: 0;
  color: var(--color-white);
  font-size: 1rem;
}

.register__container input:valid ~ label {
  color: transparent;
}

.register__container .btn {
  width: 100%;
}